import React from 'react'
import { Helmet } from 'react-helmet'
const urljoin = require('url-join')

export const ogImagePath = ( id ) => {
    return `og-pages/${id}/cover.png`
}

export const ogSiteImagePath = () => {
    return `og-pages/site/cover.png`
}
export const ogImage = ( {id, domain} )  => {
    const imageUrl = urljoin(domain, ogImagePath(id))

    return (<Helmet 
        meta={[
            {name: 'image', content: imageUrl},
            {name: 'og:image', content: imageUrl},
            {name: 'twitter:image', content: imageUrl},
        ]}
    />)
}

export default ogImage